.App {
  text-align: center;
  margin: 50px;
  padding: 0;
}
.App-logo {
  height: 40vmin;
  pointer-events: none;
}

input[type="number"] {
  margin: 5px;
  border: 2px solid #000;
  height: 25px;
}

button {
  margin: 5px;
  height: 30px;
  width: 30px;
  border: 2px solid #000;
  border-radius: 10px;
}

hr {
  width: 300px;
  border: 1px solid #000;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
